@import '../../index.scss';

.main{
  width: 100%;
  height: 100vh;
  position: relative;
  overflow-y: auto;
  transition: all 0.5s ease;
  overflow: hidden;
  &:before{
    content:'';
    position: absolute;
    z-index: 10;
    top: 0;
    left: -210%;
    width: 200%;
    height: 100%;
    filter: blur(25px);
    background-color: rgba($color: $color-blue , $alpha: .9);
    backdrop-filter: blur(10px) ;
    
  }
}
.main.active:before{
  left: 210%;
  overflow: hidden;
  transition: all 0.8s ease;
}
.main-wrap{
  width: 90%;
  margin: 0 auto;
}